import {
    isAnObject,
    lazyLoadLottie,
    loadExternalJSON,
    scrollIntoViewDoThis,
} from '$utils/helper-functions';

const getStats = (animate = true) => {
    let stats;
    if (
        localStorage.getItem('stats') && //Exists in localStorage
        typeof localStorage.getItem('stats') !== 'undefined' && //Is not undefined
        isAnObject(JSON.parse(localStorage.getItem('stats'))) === true && //parsed string is formatted as an object
        Date.now() - new Date(JSON.parse(localStorage.getItem('stats')).lastUpdated).getTime() <
        24 * 60 * 60 * 1000 //The data is less than 24 hours old
    ) {
        stats = JSON.parse(localStorage.getItem('stats'));
        console.log('stats from local', stats);
        if (animate !== false) {
            animateStats(stats);
            return;
        }
        return stats;
    } else {
        // console.log(localStorage.getItem('stats'));
        // console.log(typeof localStorage.getItem('stats'));
        // console.log(isAnObject(JSON.parse(localStorage.getItem('stats'))));
        // console.log(
        //     Date.now() -
        //         new Date(Number(JSON.parse(localStorage.getItem('stats')).lastUpdated)).getTime()
        // );
        // console.log(24 * 60 * 60 * 1000);
        //The data is more than 24 hours old. Download new data
        loadExternalJSON(
            'https://www.jumblebee.co.uk/api/auctionStats_rpvTfjHnFNF2rbWxb1jaZCAj8GGT21Nl'
        )
            .then((jsonData) => {
                stats = jsonData; // Accessing the parsed JSON
                console.log('stats from jumblebee', stats);
                //Update with new timestamp in localStorage
                stats.lastUpdated = Date.now();
                localStorage.setItem('stats', JSON.stringify(stats));
                if (animate !== false) {
                    animateStats(stats);
                    return;
                }
                return stats;
            })
            .catch((error) => {
                console.error(error);
                // Handle error if failed to load or download any of the external resources.
            });
    }
};
getStats(false);

const animateStats = (data) => {
    console.log('animating...');
    console.log(data);
    document.getElementById('stats-bids-made').innerHTML = Math.floor(
        Number(data.total_bids_made)
    ).toLocaleString('en-US');
    document.getElementById('stats-raised-to-date').innerHTML =
        '£' + Math.floor(data.total_fund_raised).toLocaleString('en-US');
    //document.getElementById('stats-auction-count').innerHTML = data.auction_count;
    document.getElementById('stats-tickets-sold').innerHTML = Math.floor(
        Number(data.tickets_sold)
    ).toLocaleString('en-US');

    const counters = document.querySelectorAll('.stats_value');

    counters.forEach((stat) => {
        // pattern used to seperate input number from html into an array of numbers and non numbers. EX $65.3M -> ["$65.3M", "$", "65", ".", "3", "M"]
        //const patt = /(\D+)?(\d+)(\D+)?(\d+)?(\D+)?/;
        const patt = /(\D+)?(\d+)(\D+)?(\d+)(\D+)?(\d+)(\D+)?(\d+)?(\D+)?/;
        const time = 100; //Delay before animating in ms
        let result = [...patt.exec(stat.textContent)];
        let fresh = true;
        let ticks;

        // Remove first full match from result array (we dont need the full match, just the individual match groups).
        result.shift();
        // Remove undefined values from result array where they didnt have a match in one of the optional regex groups
        result = result.filter((res) => res != null);

        while (stat.firstChild) {
            stat.removeChild(stat.firstChild);
        }

        for (let res of result) {
            if (isNaN(res)) {
                stat.insertAdjacentHTML('beforeend', `<span>${res}</span>`);
            } else {
                for (let i = 0; i < res.length; i++) {
                    stat.insertAdjacentHTML(
                        'beforeend',
                        `<span data-value="${res[i]}">
                <span>&ndash;</span>
                ${Array(parseInt(res[i]) + 1)
                            .join(0)
                            .split(0)
                            .map(
                                (x, j) => `
                  <span>${j}</span>
                `
                            )
                            .join('')}
              </span>`
                    );
                }
            }
        }

        ticks = [...stat.querySelectorAll('span[data-value]')];
        let activate = () => {
            let { top } = stat.getBoundingClientRect();
            let offset = window.innerHeight * 0.8;

            setTimeout(() => {
                fresh = false;
            }, time);

            if (top < offset) {
                setTimeout(
                    () => {
                        for (let tick of ticks) {
                            let dist = parseInt(tick.getAttribute('data-value')) + 1;
                            tick.style.transform = `translateY(-${dist * 100}%)`;
                        }
                    },
                    fresh ? time : 0
                );
                window.removeEventListener('scroll', activate);
            }
        };
        window.addEventListener('scroll', activate);
        activate();
    });
};



