export const isAnObject = (obj) => {
    if (typeof obj === 'object' && !Array.isArray(obj) && obj !== null) return true;
    return false;
};

export const loadExternalJSON = (url) => {
    return new Promise((resolve, reject) => {
        // Check if script already exists in DOM
        const resourceExists = document.querySelector(`link[href="${url}"]`);

        if (resourceExists) {
            resolve(); // Resource already loaded
            return;
        }

        fetch(url)
            .then((response) => response.json())
            .then((data) => {
                resolve(data); // Passes parsed JSON data as argument
            })
            .catch((error) => reject(new Error(`Error loading ${url}: ${error.message}`)));
    });
};
